.App {
  
  width: 100vw;
  height: 100vh;
  
}

boday {
margin: 0;
padding: 0;
}

.Sidebar {
  height: 100%;
  width: 250px;
  background-color: #2F4050;
}
.SidebarList {
  height: auto;
  padding: 0;
  width: 100%;

}

.SidebarList .row {

  width: 100%;
  height: 47px;
  list-style-type: none;
  margin: 0;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: center;
  align-items: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.SidebarList .row:hover{
  cursor: pointer;
  background-color: #293846;
}
.row #icon{
  flex: 30%;
  display: grid;
  place-items: center;
}
.row #title{
  flex: 70%;
}